import { Meta, StoryObj } from '@storybook/web-components';
import { html } from 'lit';

const meta: Meta = {
  title: 'Components/Avatar',
  component: 'ds-avatar',
  argTypes: {
    image: { control: 'text' },
    label: { control: 'text' },
    size: {
      control: 'select',
      options: ['xx-small', 'x-small', 'small', 'medium', 'large']
    },
    shape: {
      control: 'select',
      options: ['circle', 'square', 'rounded']
    },
  },
  parameters: {
    docs: {
      description: {
        component: 'A versatile avatar component that supports images, initials, and various sizes and shapes.'
      }
    }
  }
};

export default meta;

type Story = StoryObj;

export const Default: Story = {
  args: {
    image: 'https://placehold.co/600x400/000000/FFFFFF/png',
    label: 'John Doe',
    size: 'medium',
    shape: 'circle',
  },
  render: (args) => html`
    <ds-avatar image=${args.image} label=${args.label} size=${args.size} shape=${args.shape}></ds-avatar>
  `
};

